import { useEffect } from "react";
import { apiSectionFilters } from "../config";
import { SORT_BY } from "../store/slices/newsSortSlice";
import { useAppDispatch } from "../store/store";

export const useResetSort = () => {
  const dispatch = useAppDispatch();
  useEffect(() => {
    const sortHandler = async () => {
      await dispatch(SORT_BY(apiSectionFilters.ORDER_BY_NEW));
    };
    sortHandler();
  }, []);
  return "";
};
